<template>
    <div ref="wrapper" class="wrapper">
        <div class="content">
            <slot class="content"></slot>
        </div>
        <div class="textScroll top" v-if="pullDownRefresh && data.length"></div>
        <div class="textScroll bottom" v-if="pullUpLoad">{{isMore?pullUpLoad.moreTxt:pullUpLoad.noMoreTxt}}</div>
    </div>
</template>

<script>
    import BScroll from 'better-scroll'
    export default {
        name:'Scroll',
        props: {
            /**/
            //
            isMore:{
                type: Boolean,
                required: true
            },
            //
            data: {
                type: Array,
                required: true
            },

            /**/
            /*
            * 123
                
                 probeType  1 scroll 
                 probeType  2  scroll 
                 probeType  3  momentum  scroll 
            * */
            probeType:{
                type: Number,
                default: 0
            },
            //click
            click: {
                type: Boolean,
                default: true
            },
            //
            pullUpLoad:{
                type: [Object,Boolean],
                default: function () {
                    return {
                        threshold: -30,  //
                        moreTxt: '',
                        noMoreTxt: '',
                    }
                },
            },
            //
            pullDownRefresh: {
                type: [Object,Boolean],
                default: function () {
                    return {
                        threshold: 30, //
                        stop: 0  //
                    }
                },
            },
            //
            refreshDelay:{
                type: Number,
                default: 100
            },
            //myScroll
            myScroll:{
                type: Function,
            },
            myScrollEnd:{
                type: Function,
            },
            getScrollObj:{
                type: Function,
            }
        },
        data:function () {
            return{
                isLock:true,
            }
        },
        mounted() {
            // DOMbetter-scroll
            setTimeout(() => {
                this._initScroll()
            }, 20)
        },
        methods: {
            _initScroll() {
                if (!this.$refs.wrapper) {
                    return
                }
                // better-scroll
                this.scroll = new BScroll(this.$refs.wrapper, {
                    probeType: this.probeType,
                    click: this.click,
                    pullUpLoad: this.pullUpLoad,
                    pullDownRefresh: this.pullDownRefresh,
                });
                window.aa1 = this.scroll;

                /**/
                if(this.pullUpLoad){
                    this.scroll.on('pullingUp', () => {
                        if(!this.isMore || !this.isLock){
                            return false;
                        }
                        this.$emit('pullingUp')
                    });
                }
                /**/
                if(this.pullDownRefresh){
                    this.scroll.on('pullingDown', () => {
                        if(!this.isLock){
                            return false;
                        }
                        this.$emit('pullingDown');
                    });
                }
                //
                if(this.myScroll){
                    this.scroll.on('scroll', (o) => {
                        this.myScroll(o);
                    });
                }
                if(this.myScrollEnd){
                    this.scroll.on('scrollEnd', (o) => {
                        this.myScrollEnd(o);
                    });
                }
                if(this.getScrollObj){
                    this.getScrollObj(this.scroll);
                }
            },
            disable() {
                // better-scrolldisable
                this.scroll && this.scroll.disable()
            },
            enable() {
                // better-scrollenable
                this.scroll && this.scroll.enable()
            },
            refresh() {
                // better-scrollrefresh
                this.scroll && this.scroll.refresh()
            },
            finishPullUp() {
                // better-scrollrefresh
                this.scroll && this.scroll.finishPullUp()
            },
            finishPullDown() {
                // better-scrollrefresh
                this.scroll && this.scroll.finishPullDown()
            },
            scrollTo() {
                // better-scrollscrollTo
                this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
            },
            scrollToElement() {
                // better-scrollscrollToElement
                this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
            }
        },
        watch: {
            // refreshDelayrefresh
            data() {
                this.isLock = false;
                setTimeout(() => {
                    this.refresh();
                    this.finishPullUp();
                    this.finishPullDown();
                    this.isLock = true;
                }, this.refreshDelay)
            },
        }
    }
</script>
<style lang="scss" scoped>
    .wrapper{
        height: 100%;
        overflow: hidden;
        position: relative;
        z-index: 1;
    }
    .content{
        height: auto;
        clear: both;
        overflow: hidden;
        position: relative;
        z-index: 2;
    }
    .textScroll{
        position: absolute;
        left: 0;
        z-index: 1;
        height: 32px;
        line-height: 32px;
        text-align: center;
        width: 100%;
        font-size: 14px;
        font-weight: 600;
    }
    .top{
        top: 0;
    }
    .bottom{
        bottom: 0;
    }
</style>