<template>
    <div class="userUpdateItem">
        <!---->
        <div class="sexItem item" v-if="name==='sex'">
            <ul>
                <li @click="sex=''">
                    <span class="label"></span>
                    <span class="value">
                        <i :class="'icon '+ (sex==''?'active':'')"></i>
                    </span>
                </li>
                <li @click="sex=''">
                    <span class="label"></span>
                    <span class="value">
                        <i :class="'icon '+ (sex==''?'active':'')"></i>
                    </span>
                </li>
            </ul>
        </div>

        <!---->
        <div class="birthItem item" v-if="name==='birth'">
            <ul>
                <li @click="openBirthPicker">
                    <span class="label"></span>
                    <span class="value">{{birth?moment(birth).format("YYYY-MM-DD"):''}}</span>
                </li>
            </ul>
            <mt-datetime-picker
                    v-model="birth"
                    ref="birthPicker"
                    type="date"
                    :startDate="date.startDate"
                    :endDate="date.endDate"
                    minuteFormat="YYYY-MM-DD"
                    year-format="{value}"
                    month-format="{value}"
                    date-format="{value}"
                    @confirm="birthConfirm"
            >
            </mt-datetime-picker>
        </div>
        <!---->
        <div class="passwordItem item" v-if="name==='password'">
            <div class="input">
                <input type="password" placeholder="6" maxlength="6" v-model="password.value"/>
            </div>
            <div class="input">
                <input type="password" placeholder="" maxlength="6" v-model="password.againValue"/>
            </div>
        </div>
        <!---->
        <div class="button-bar" v-if="name">
            <a href="javascript:" @click="updateDate"></a>
        </div>
    </div>
</template>

<script>
    export default {
        name: "UserUpdateItem",
        components: {

        },
        mounted() {
            this.getUserData();
        },
        data(){
            return{
                name: this.$route.query.name||"sex",
                birth:'',
                mobile:'',
                sex:'',
                date:{
                    startDate: new Date(-946800000000), //1940
                    endDate:new Date(),
                },
                password:{
                    value:'',
                    againValue:''
                }
            }
        },
        methods:{
            getUserData:function(){
                this.Service.IMemberService.GetMemberInfoByToken().then(d=>{
                    console.log("dddd",d);
                    this.mobile = d.mobile;
                    this.sex = d.sex;
                    this.birth = d.birthday;
                })
            },
            openBirthPicker() {
                this.$refs.birthPicker.open();
            },
            birthConfirm(v){
                console.log("vvv",v);
            },
            updateDate:function(){
                let tPromise = null;
                if(this.name==='birth'){
                    tPromise = this.updateBirthday();
                }else if(this.name==='sex'){
                    tPromise = this.updateSex();
                }else if(this.name==='password'){
                    tPromise = this.updatePassword();
                }
                if(tPromise){
                    tPromise.then(d=>{
                        console.log(d);
                        if(d.count===1){
                            this.toast('');
                            setTimeout(()=>{
                                history.back();
                               // this.goUrl('/user/information');
                            },2000);
                        }
                    })
                }

            },
            updateBirthday:function(){
                if(!this.birth){
                    this.toast('');
                }
                return this.Service.IMemberService.UpDateBirthday({birthday:this.moment(this.birth).format("YYYY-MM-DD")})
            },
            updatePassword:function(){
                const reg =newRegExp("^[0-9]{6}$");
                if(!reg.test(this.password.value)){
                    this.toast('6');
                    return false
                }else if(this.password.value!==this.password.againValue){
                    this.toast('');
                    return false
                }else{
                    return this.Service.IMemberService.ResetPassword({pwd:this.password.value});
                }
            },
            updateSex:function(){
                if(!this.sex){
                    this.toast('');
                }
                return this.Service.IMemberService.UpdateSex({sex:this.sex})
            },
            goUrl(url,params){
                this.$router.push({
                    path: url
                })
            },
        }
    };
</script>
<style lang="scss" scoped>
    .userUpdateItem{
        background-color: #F4F4F4;
    }
    .item{
        margin-top: 20px;
    }
    .input{
        padding: 8px 24px 6px 16px;
        margin: 0 10px 20px;
        background-color: #fff;
        border:1px solid rgb(221, 221, 221);
        input{
            height: 34px;
            color: #111;
            font-size: 14px;
            border: none;
            display: block;
            width: 100%;
        }
    }
    ul{
        border-top: 1px solid #ddd;
    }
    li{
        padding: 16px;
        overflow: hidden;
        color: #3a3a3a;
        font-size: 16px;
        background-color: #fff;
        clear: both;
        line-height: 20px;
        border-bottom:1px solid #ddd;
        span{
            display: inline-block;
        }
        .value{
            float: right;
            i{
                display: inline-block;
                padding: 6px;
                border-radius: 50%;
            }
            i.active{
                background-color: #ffc900;
            }
        }
    }
    .button-bar{
        padding: 20px 10px;
        a{
            display: block;
            height: 44px;
            text-align: center;
            line-height: 44px;
            background-color: red;
            color: #333;
            font-size: 16px;
        }
    }
</style>

